\"Aclonica\", sans-serif;\n$color-text: #32404e;\n$color-main: #b3404a;\n$color-bg: #fefefe;\n$color-nav: #fff5ee;\n$color-hover: #f77984;\n\n* {\n  margin: 0;\n  padding: 0;\n  box-sizing: border-box;\n}\n\nhtml {\n  scroll-padding-top: 120px;\n}\n\nbody {\n  font-family: $font-main;\n  color: $color-text;\n  background-color: $color-bg;\n\n  a {\n    text-decoration: none;\n    cursor: pointer;\n  }\n\n  a:link,\n  :visited {\n    color: $color-text;\n  }\n\n  a:hover {\n    color: $color-main;\n    font-weight: bold;\n  }\n}\n\n@mixin sectionStyle {\n  width: 100vw;\n  height: auto;\n  display: flex;\n  justify-content: center;\n}\n\n@mixin dataStyle {\n  width: 15%;\n  height: auto;\n  min-height: 150px;\n  background-color: $color-main;\n  display: flex;\n  flex-direction: column;\n  justify-content: center;\n  align-items: center;\n\n  @media screen and (max-width: 1200px) {\n    width: 33.33%;\n  }\n\n  @media screen and (max-width: 470px) {\n    width: 80%;\n  }\n\n  .number {\n    width: 100%;\n    height: 30%;\n    display: flex;\n    justify-content: center;\n    align-items: center;\n    font-size: 1.9rem;\n    color: $color-bg;\n  }\n\n  .unit {\n    width: 100%;\n    height: 15%;\n    display: flex;\n    justify-content: center;\n    align-items: center;\n    font-size: 1.3rem;\n    color: $color-bg;\n  }\n}\n\n@mixin headingStyle {\n  font-size: 1.7rem;\n  font-weight: 600;\n  text-transform: capitalize;\n  text-align: center;\n  margin: 50px;\n\n  @media screen and (max-width: 768px) {\n    font-size: 1.5rem;\n  }\n\n  @media screen and (max-width: 505px) {\n    margin-top: 100px;\n  }\n}\n\n.title-line {\n  height: 10px;\n  margin: 0 auto;\n  width: 80%;\n  display: flex;\n  background-color: $color-hover;\n  margin-top: 10px;\n  border-radius: 5px;\n}\n","header {\n  width: 100vw;\n  height: 100px;\n  display: flex;\n  flex-direction: row;\n  justify-content: center;\n  background-color: $color-nav;\n  margin-bottom: 40px;\n  box-shadow: 0px 0px 3px 0px rgba(0, 0, 0, 0.3);\n  position: sticky;\n  z-index: 3;\n  top: 0;\n\n  nav {\n    width: 80%;\n    height: 100%;\n    display: flex;\n    flex-direction: row;\n    justify-content: space-between;\n    align-items: center;\n\n    @media (max-width: 1430px) {\n      width: 95%;\n    }\n\n    .main-menu {\n      width: 50%;\n      height: 100%;\n      display: flex;\n      flex-direction: row;\n      justify-content: space-evenly;\n      align-items: center;\n      list-style: none;\n\n      @media (max-width: 1024px) {\n        display: none;\n      }\n\n      li {\n        text-transform: uppercase;\n        font-weight: 600;\n      }\n    }\n\n    .navbar-search {\n      cursor: pointer;\n\n      @media (max-width: 1024px) {\n        display: none;\n      }\n    }\n  }\n}\n","footer {\n  background-color: $color-main;\n  height: 40px;\n  display: flex;\n  flex-direction: row;\n  justify-content: center;\n  align-items: center;\n}\n\nfooter p {\n  display: flex;\n  flex-direction: row;\n  color: $color-bg;\n  text-align: center;\n  font-size: 0.8rem;\n}\n",".business-section {\n  @include sectionStyle;\n  min-height: 600px;\n  margin-top: 180px;\n  margin-bottom: 100px;\n\n  .business-container {\n    width: 90%;\n    height: auto;\n    display: flex;\n    flex-direction: column;\n    justify-content: center;\n\n    .business-title {\n      width: 100%;\n      height: 100px;\n      display: flex;\n      justify-content: center;\n      align-items: end;\n\n      h3 {\n        @include headingStyle;\n      }\n    }\n\n    .business-content-box {\n      width: 100%;\n      height: auto;\n      min-height: 500px;\n      display: flex;\n      flex-direction: row;\n      justify-content: center;\n      align-items: center;\n\n      @media screen and (max-width: 1130px) {\n        flex-wrap: wrap;\n      }\n\n      .business-case {\n        width: 25%;\n        height: auto;\n        min-height: 450px;\n        display: flex;\n        flex-direction: column;\n       